}
.tgl + .tgl-btn {
  outline: 0;
  display: block;
  width: 10em;
  height: 2em;
  position: relative;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
